import React from 'react';

class Modal extends React.Component {

	constructor(props){
		super(props);
		this.state = { show: true };

	}

	clickOut = (e) => {
		if ( e.target.id == "modal" ) this.props.evnt(null);
	};

	render ( ) {

		return ( 
			<div id="modal" onClick={ this.clickOut } >
			<div id="modal-inner" style={ this.props.style } >
			<div id="close" onClick={ this.props.evnt.bind(null,null) }>&times;</div>
			<>
			{ this.props.children }
			</>
			</div>
			</div>
		);

	}

}

export default Modal;
