import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import IMG_LEAVE from '../images/leave.png';
import IMG_WALLET from '../images/wallet.svg';
import IMG_MARKET from '../images/market.svg';
import IMG_NFT from '../images/nft.png';

//const quote = "A legend's path to guide your own";
const quote = "The restless spirits long to guide";
const Header = ({ siteTitle, context, address, isConnected, eventHandler }) => (
    <>
    <div style={{maxWidth:`1080px`,textAlign:`center`,margin:`auto`,position:`relative`}}>
    { !isConnected && <em id='quote'>{quote}</em> }

    <button style={{position:`absolute`,top:`0`,right:`0`,fontWeight:`bolder`,margin:`0.5em`,height:`2em`}}
    type="button"
    onClick={ e => eventHandler( "connect" ,{} ) }
    >{ isConnected ? < img src={IMG_LEAVE} style={{height:`100%`}} /> : <>Connect<img src={IMG_NFT} style={{height:`1rem`}}/></> }</button>


    { (isConnected) &&
        <div style={{display:"flex",justifyContent:"center"}}>
	 <button style={{margin:`0.5em`,height:`2em`}}
            onClick={ e => eventHandler( "context" ,{} ) } >
                <img src={ context === "market" ? IMG_WALLET : IMG_MARKET } style={{height:`100%`}} /> </ button>
        <button style={{margin:`0.5em`}}
        type="button"
        ><span style={{
            maxWidth: `9em`,
                overflow: `hidden`,
                textOverflow: `ellipsis`,
                display: `block`,
                whiteSpace: `nowrap`
        }}>{ address }</span></button></div>}





    </div>
    { (!isConnected) &&
        <nav>
        <div style={{
            margin: `0 auto`,
                maxWidth: 1080,
        }}>
        <ul>
        <li><Link to="/books">Books</Link></li>
        <li><Link to="/excerpts">Excerpts</Link></li>
        <li><Link to="/blog">Blog</Link></li>
        <li><Link to="/contact">Contact</Link></li>
        </ul>
        </div>
        </nav> }
    </>
)

Header.propTypes = {
    siteTitle: PropTypes.string,
}

Header.defaultProps = {
    siteTitle: ``,
}

export default Header
