import React, { useEffect, useState } from 'react';
/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import PropTypes from "prop-types"
import { useAccount,
	useNetwork,
	useDisconnect,
	configureChains,
	createConfig,
	WagmiConfig } from 'wagmi'
import {
	readContract,
	prepareWriteContract,
	writeContract
} from '@wagmi/core'
import { useWeb3Modal } from '@web3modal/react'
import { parseEther, formatEther } from 'viem'
import IMG_MARKET from '../images/market.svg';
import '../globals'
import { StaticQuery, graphql, Link } from "gatsby"
import CONTRACT_DATA_NFT from '../contracts/NFT.json';
import CONTRACT_DATA_MARKET from '../contracts/NFTMarket.json';
import Hourglass from "./hourglass"
import Header from "./header"
import Modal from "./modal"
import "./layout.css"

import { EthereumClient, w3mConnectors, w3mProvider } from '@web3modal/ethereum'
import { Web3Modal } from '@web3modal/react'
import { harmonyOne } from 'wagmi/chains'


const ABI_NFT = CONTRACT_DATA_NFT.abi;
const ABI_MARKET = CONTRACT_DATA_MARKET.abi;
const contracts = {
	"harmony":{
		"nft" : "0x91BD719a249F6AAFDE5850392e1229e962bf3Df0",
		"market":"0x22bF9B8f4568be87b0557c30505fCba698B06367",
	}
}

const chains = [ harmonyOne ];
const projectId = '061d544ba5120e2746c84afb1a9e0adb';
const { publicClient } = configureChains(chains, [w3mProvider({ projectId })])
const wagmiConfig = createConfig({
	autoConnect: true,
	connectors: w3mConnectors({ projectId, chains }),
	publicClient
})
const ethereumClient = new EthereumClient(wagmiConfig, chains)
const SYMBOL = "Â¢";

const Toast = ( props ) => { return <div className="toast"><strong>{ props.str }</strong></div> };

const Nft = ( props ) => {
	if ( props.token === undefined ) {
		console.log( props );
		return <div className="nft">None</div>
	} else return  <div className="nft"
	onClick={ e => props.eventHandler() }
	title={ props.token.name } style={ props.style ? props.style : {} }  >
		<img src={props.token.image} alt={ props.token.description} />
		</div>
};

const NftView = ( props ) => {
	const [more,setMore] = useState(false);
	const [showSellForm,setShowSellForm] = useState(false);
	const [approved,setApproved] = useState(false);
	const [price,setPrice] = useState( (props.marketInfo ? props.marketInfo.price : 0) );

	useEffect(() => {

		console.log( props.marketInfo );
		console.log( formatEther( price ) );

	    if ( props.isConnected && ! props.marketInfo ) {
	        props.getApprovedNFT( props.token.id ).then( r => {
			setApproved( r );
		}).catch( e => {
		    setApproved( false );
		});
	    }
	}, [props])

	const styles = {
		img : {
			width: `42vh`,
			margin: `1em auto`,
			display: `block`,
			border: `0.5em solid var(--main)`,
			borderRadius: `1em` },
		btnSell:{ borderRadius:`2em`,margin:`0.5em`,boxShadow: `0 0 4px 1px  var(--dark)`,borderColor: `var(--emphasis)`},
		btnApprove: { margin:`1em`,textAlign:`center`},
		details: {
			overflow: `auto`,
			width: `90%`,
			margin: `auto`,
			display: `block`,
			borderRadius: `1em`,
			height: `5em`
		}
	};

	if ( props.token === undefined ) return <div className="nft">None</div>
		else return  <div title={ props.token.name } style={ props.style ? props.style : {} }  >
		<img src={ props.token.image} alt={ props.token.description}
	style={ styles.img }/>
		{ more ? <>  { ( !props.marketInfo ) ?  ( !showSellForm ? <button style={ styles.btnSell} onClick={ e => setShowSellForm( true ) }><img src={IMG_MARKET} alt="nft market" style={{height:`1rem`}} /> </ button> :

				<div style={ styles.btnApprove }><button disabled={ approved } onClick={ e => {
					props.approveNFT( props.token.id ).then( r => setApproved( r ) ).catch( e => console.error(e) )
				} } type="button" className="action" style={{width:`30%`}}>Approve</button><input type="number" disabled={ !approved } onBlur={ e => setPrice( e.target.value ) } placeholder="price" style={{width:`30%`}}/><button disabled={ !approved && price <= 0 } onClick={ e => {
					props.sellNFT( props.token.id , parseEther( price ) );
				} } className="action" type="button" style={{width:`30%`}} >sell</button></div> ) :  null }

			<textarea readOnly={true} value={ JSON.stringify( props.token) }
			style={styles.details}
			></textarea></>  :
			( props.marketInfo ? <div style={{margin:`auto`,display:`block`,textAlign:`center`}} ><button type="button" className="action" style={{ background:`var(--light)`,borderColor:`var(--alert)` }} onClick={ e => props.buyNFT( props.marketInfo.itemId , props.marketInfo.price ) } >BUY</button><span style={{margin:`1em`}}>{ formatEther( props.marketInfo.price ) }<em>{SYMBOL}</em></span></div> : <button type="button" className="action" onClick={ e => setMore( true ) }>. . .More</button> ) }
		</div>
}

function TitledLayout( props ) {

	const [toast,setToast] = useState(null);
	const [spinner,setSpinner] = useState(false);
	const [myNFTs,setMyNFTs] = useState([]);
	const [marketItems,setMarketItems] = useState([]);
	const [nfts,setNFTs] = useState({});
	const [context,setContext] = useState("wallet");
	const [view,setView] = useState(null);
	const { address, isConnected } = useAccount()
	const { chain, chains } = useNetwork()
	const { open, close } = useWeb3Modal()
	const { disconnect } = useDisconnect()

	const init = async () => {
	    if (spinner) return;
	    console.log(`init(); account: ${address}, chain : ${chain.network}`);
	    setSpinner( true );
	    callWalletOfOwner().then( r => {
	        callFetchMarketItems().then( r => {					
	            setSpinner(false);
	        }).catch( e => errorHandler("callFetchMarketItems",e ) && setSpinner(false) );
	    }).catch( e => {
		 errorHandler("callWalletOfOwner",e );
		  console.error( e );
		  console.log( chain );
		  setSpinner(false);
		  disconnect();
	    });
	}

	useEffect(() => {
		if ( isConnected && undefined !== chain && undefined !== address ) init();
	}, [chain, isConnected, address ]);

	const eventHandler = ( what , data ) =>  {
		if ( null == what ) setView(null);
		switch (what) {
			case "connect": {
				if ( !isConnected ) open();
				else open();//disconnect();
			} break;
			case "nft": {
				setView(data);
			} break;
			case "context": {
				if ( context === "market" ) {

					if ( myNFTs.length === 0 ) doToast("First buy some NFTs");
					else setContext("wallet");
				} else setContext("market");
			} break;
		}
	}

	const callFetchMarketItems = ( ) => {
		return new Promise( (resolve, reject) => {
			readContract({
				address: contracts[ chain.network ]['market'],
				abi: ABI_MARKET,
				functionName: 'fetchMarketItems',
			}).then( (rslt) => {
				let nftIDs = [];
				const allNfts = nfts;
				let marketItemsSnapshot = [];
				rslt.forEach( i => {
					marketItemsSnapshot.push(
					    {
						itemId: i["itemId"],
						nftContract: i["nftContract"],
						owner:i["owner"],
						price:i["price"],
						seller:i["seller"],
						sold:i["sold"],
						tokenId:i["tokenId"]
					    }
					);
					if ( ! ( i["tokenId"] in nfts ) ) nftIDs.push( i["tokenId"] );
					else {
						
						console.log(`id: ${ i["tokenId"] } - ${ ( i["tokenId"] in nfts ) }  `);
					}
				} );
				Promise.all( nftIDs.map( async (anID) => {
					readContract({
						address: contracts[ chain.network ]['nft'],
						abi: ABI_NFT,
						functionName: 'tokenURI',
						args: [ anID ]
					}).then( ( data ) => {
						const nftsSnapshot = nfts; 
						nftsSnapshot[ anID ] = (data.startsWith("data") ? dataURLDecode( data ) : data);
						console.log( data );
						if ( Object.keys(nftsSnapshot).length === 0 ) console.log( "1@@@@@@@@@@@@@@@ EMPTY!" );
						setNFTs( prev => ({ ...prev, ...nftsSnapshot }));
					}).catch(e => errorHandler("tokenURI",e) && reject() );
				})).then( r => {
				        setMarketItems( marketItemsSnapshot );
					resolve();
				} ).catch( e => errorHandler("tokenURI",e) && reject() );
			} ).catch( e => errorHandler("fetchMarketItems",e) && reject() );
		});
	}

	const callWalletOfOwner = ( ) => {
		return new Promise( (resolve, reject) => {
			readContract({
				address: contracts[ chain.network ]['nft'],
				abi: ABI_NFT,
				functionName: 'walletOfOwner',
				args: [ address ]
			}).then( (rslt) => {
				const nftIDs = rslt.filter( i => ! ( i in nfts ) );
				Promise.all( nftIDs.map( async (anID) => {
					await readContract({
						address: contracts[ chain.network ]['nft'],
						abi: ABI_NFT,
						functionName: 'tokenURI',
						args: [ anID ]
					}).then( ( data ) => {
						const nftsSnapshot = nfts; 
						nftsSnapshot[ anID ] = (data.startsWith("data") ? dataURLDecode( data ) : data);
						if ( Object.keys(nftsSnapshot).length === 0 ) console.log( "2@@@@@@@@@@@@@@@ EMPTY!" );
						setNFTs( prev => ({ ...prev, ...nftsSnapshot }));
					}).catch(e => errorHandler("tokenURI",e) && reject() );
				}) ).then( () =>  {
					setMyNFTs( rslt );
					if ( rslt.length === 0 ) setContext("market");
					resolve();
				} ).catch( e => errorHandler("tokenURI",e) && reject() );
			} ).catch( e => errorHandler("walletOfOwner",e) && reject() );
		});
	}

	const dataURLDecode = ( url ) => {
		try{
			let tmp = url.split(',');
			let type = tmp[0].split(":")[1];
			if (type === "application/json;base64" ){
				let dataStr = atob(tmp[1]);
				return JSON.parse ( dataStr );
			};
		} catch (err) {
			console.error(err);
		}
		return "";
	};

	const callMarketApprovedNFT = ( id ) => {
		return new Promise( (resolve, reject) => {
			readContract({
				address: contracts[ chain.network ]['nft'],
				abi: ABI_NFT,
				functionName: 'getApproved',
				args: [ id ]
			}).then( ( data ) => {
				for (let i = 0; i < data.length; i++) {
					if ( data[i] === contracts[ chain.network ]["market"] ) resolve(true);
				}
				resolve( false );
			}).catch( e => errorHandler("callMarketApprovedNFT",e) && reject( ) )
		});
	}

	const marketApproveNFT = ( id ) => {
		return new Promise( (resolve, reject) => {
		   prepareWriteContract({
                	address: contracts[ chains.network ]['nft'],
                        abi: ABI_NFT,
                        functionName: 'approve',
                        args: [ contracts[ chains.network]["market"], id ],
                    }).then( ({request}) => {
                        writeContract(request).then( (rsp) => {
                            resolve( true );
                        }).catch( e => errorHandler("marketApproveNFT",e) && reject(e) );
                    }).catch( e => errorHandler("marketApproveNFT",e) && reject(e) );
		});
	}

	const marketSellNFT = ( id , wei) => {
		console.log( `marketSellNFT( ${ contracts[ chains.network ]["nft"] } , ${ id } , ${ wei } )`);
		prepareWriteContract({
                	address: contracts[ chains.network ]['market'],
                        abi: ABI_MARKET,
                        functionName: 'createMarketItem',
                        args: [ contracts[ chains.network]["nft"], id, wei ],
                }).then( ({request}) => {
                        writeContract(request).then( (rsp) => {
                            if ( rsp?.events?.MarketItemCreated ){
				    const i = rsp.events.MarketItemCreated.returnValues;
				    const tkn = i.tokenId;
				    setMarketItems( prev => ([ ...prev,
					    {
						itemId: i.itemId,
						nftContract:i.nftContract,
						owner:i.owner,
						price:i.price,
						seller:i.seller,
						sold:i.sold,
						tokenId:i.tokenId
					    }
					]));
				   setMyNFTs( myNFTs.filter( i => tkn !== i ) );
	                   }
			}).catch( e => errorHandler("createMarketItem",e) );
		}).catch( e => errorHandler("createMarketItem",e) );
	}

	const marketBuyNFT = ( id , wei ) => {
		console.log( `marketBuyNFT( ${ contracts[ chain.network ]["nft"]  } , ${ id } )`);
		prepareWriteContract({
                	address: contracts[ chains.network ]['market'],
                        abi: ABI_MARKET,
                        functionName: 'createMarketSale',
                        args: [ contracts[ chains.network]["nft"], id ],
			value: wei
                }).then( ({request}) => {
                        writeContract(request).then( (rsp) => {
				if ( rsp.hasOwnProperty("transactionHash") ) {
					const tkn = marketItems.filter( i => id === i["itemId"] )[0]["tokenId"];
		    			setMyNFTs( prev => ([ ...prev, tkn ]) );
		    			setMarketItems( marketItems.filter( i => id !== i["itemId"] ) );
				}
			}).catch( e => errorHandler("createMarketSale",e) );
		}).catch( e => errorHandler("createMarketSale",e) );
	
	}

	const errorHandler = ( s , e ) => {
		console.log(e);
		if (s) doToast(`Error calling ${s}`);
	}

	const doToast = ( s , t=4000 ) => {
		setToast( s );
		setTimeout( () => setToast(null), t );
	}

	const MyNFTsView = () => {
		return <> { myNFTs.map( (i,k) => <Nft key={k} token={ nfts[ i ] }
			eventHandler={ eventHandler.bind(null, "nft", nfts[ i ] ) } /> ) } </>
	}

	const MarketNFTsView = () => <> { marketItems.map( (i,k) => <Nft key={k} token={ nfts[ i["tokenId"] ] }
			eventHandler={ eventHandler.bind(null, "nft", nfts[ i["tokenId"] ] ) } />
		)}
			</>

	const Footer = () => { return <footer>
			<em>&copy; <Link to="/christian-lacdael" style={{color:`var(--main)`,textDecoration:`revert`}}>Christian Lacdael</Link> { (new Date()).getFullYear() }</em>
			</footer> }

	const dappAddress = () => {

		if ( undefined === chain ) return "...";
		if ( chain.network in contracts ) return contracts[ chain.network ][ context === "wallet" ? "nft": "market" ];
	}

	return (
		<>
		<Header
		siteTitle={ props.title || `Title`}
		context={ context }
		isConnected={ isConnected }
		address={ dappAddress() }
		eventHandler={ eventHandler } />
		<div style={{
			margin: `0 auto`,
				maxWidth: 1080,
				padding: `0 1.0875rem 1.45rem`,
		}} >
		{ view != null && <Modal
			evnt={ eventHandler} >
			{ ( "image" in view ) ?
				<NftView token={ view }
				isConnected={ isConnected }
				marketInfo={( myNFTs.includes( view.id ) ) ? null  : marketItems.filter( (i) => i["tokenId"] === view.id )[0]  }
				approveNFT={ marketApproveNFT }
				getApprovedNFT={ callMarketApprovedNFT }
				sellNFT={ marketSellNFT }
				buyNFT={ marketBuyNFT }
				eventHandler={ eventHandler }
				/> : null } </Modal> }
		{ ( null !== toast ) && <Toast str={toast} /> }
		{ ( isConnected ) ?  ( ( context === "market" ) ? ( marketItems.length > 0 ? MarketNFTsView() : <Hourglass /> ) : MyNFTsView() )
				: <><main>{ props.children }</main><Footer /></>
		}
		</div>
		</>
	)
}


TitledLayout.propTypes = {
	children: PropTypes.node.isRequired,
}

const Layout = ( props ) => {
	return(
		<> <WagmiConfig config={wagmiConfig}>
		<StaticQuery
		query={graphql`
	query SiteTitleQuery {
      site {
	siteMetadata {
	  title
	}
      }
    }
  `} render={ data => <TitledLayout title={data.site.siteMetadata.title} children={props.children} /> }
		/>

		</WagmiConfig>
		<Web3Modal projectId={projectId} ethereumClient={ethereumClient} defaultChain={harmonyOne} />
		</>
	);
}

export default Layout
